@import "@/styles/main.scss";


































































































    .container-fluid{
      background-color: #ffffff;
      margin-top: 40px;

    }
    .top-heading{
        padding-top: 70px;
        padding-bottom: 40px;
        font-family: 'Helvetica';
        font-size: 40px;
        font-weight: 700;
        color: #000000;
        cursor: default;

    }

    .how-it-works-pinked{
        padding-top: 70px;
        padding-bottom: 40px;
        color: #F07A6A;
        font-size: 40px;
        font-weight: 700;
        margin-left: 14px;
        cursor: default;

    }

    .card-container{
        width: 23%;
        margin-bottom:80px;
    }

    .all-cards-container-div{
        flex-wrap: wrap;
        padding: 0 49px;
    }

@media (max-width: 1020px){

    .card-container{
        width: 40% !important;
        margin-bottom:40px
    }

    .all-cards-container-div{
        flex-wrap: wrap;
    }
}

@media (max-width: 904px){

    .card-container{
        width: 45% !important;
        margin-bottom:40px
    }

    .all-cards-container-div{
        flex-wrap: wrap;
    }
}

@media (max-width: 815px){

    .card-container{
        width: 47% !important;
        margin-bottom:40px
    }

    .all-cards-container-div{
        flex-wrap: wrap;
    }
}

@media (max-width: 783px){

    .card-container{
        width: 49% !important;
        margin-bottom:40px
    }

    .all-cards-container-div{
        flex-wrap: wrap;
    }
}

@media (max-width: 650px){
    .card-container{
        width: 100% !important;
        margin-bottom:40px;
    }

    .all-cards-container-div{
      padding: 0 21px !important;
    }

    .top-heading{
      font-size: 2rem !important;
      padding-bottom: 0px !important;

    }

    .how-it-works-pinked{
      font-size: 2rem !important;
      padding-bottom: 0px !important;

    }
}

@media (min-width: 1370px){

  .top-heading{
      padding-top: 90px !important;
  }
  .how-it-works-pinked{
      padding-top: 90px !important;
  }
}
